<template>
  <v-container fill-height fluid>
    <v-col
      col-12
      col-lg-6
      col-md-8
      col-sm-12
      align="center"
      justify="center"
      class="component-container"
    >
        <v-card class="login-component" raised elevation="12">
          <v-card-text>
            <h1 color="primary">Registro</h1>
          </v-card-text>
          <v-card-actions>
            <v-col>
              <v-form ref="form" v-model="valid">
                <v-text-field v-on:keydown.enter.prevent="postRegistration" type="text" label="Nombre" outlined required v-model="name"></v-text-field>
                <v-text-field v-on:keydown.enter.prevent="postRegistration" type="text" label="Apellido" outlined required v-model="lastName"></v-text-field>
                <v-text-field
                v-on:keydown.enter.prevent="postRegistration"
                  type="email"
                  label="Correo Electronico"
                  outlined
                  required
                  v-model="email"
                ></v-text-field>
                <v-text-field
                v-on:keydown.enter.prevent="postRegistration"
                  type="password"
                  label="Contraseña"
                  :rules="passwordRules"
                  outlined
                  required
                  v-model="password"
                ></v-text-field>
                <v-text-field
                v-on:keydown.enter.prevent="postRegistration"
                  type="password"
                  label="Confirma contraseña"
                  :rules="passwordConfirmationRules"
                  outlined
                  required
                  v-model="passwordConfirmation"
                ></v-text-field>
                <v-btn
                  type="button"
                  :disabled="!valid"
                  raised
                  color="primary"
                  @click="postRegistration"
                  large
                  block
                >Enviar</v-btn>
              </v-form>
            </v-col>
          </v-card-actions>
        </v-card>
    </v-col>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import store from "@/store";

export default {
  data: function() {
    return {
      valid: true,
      phoneNumber: null,
      email: null,
      lastName: null,
      name: null,
      password: null,
      passwordConfirmation: null,
      dataRegisterUser: {},
      passwordConfirmationRules: [
        confirmation =>
          confirmation === this.password || "Las contraseñas deben coincidir"
      ],
      passwordRules: [
        v => !!v || "La contraseña es requerida",
        v =>
          (v && v.length >= 8) ||
          "La contraseña debe tener al menos 8 caracteres"
      ]
    };
  },
  methods: {
    ...mapActions("login", ["registerUserAction"]),
    postRegistration() {
      if (this.$refs.form.validate()) {
        this.phoneNumber = store.getters["login/phoneNumber"];
        const code = store.getters["login/verificationCode"];
        this.dataRegisterUser.phone_number = this.phoneNumber;
        this.dataRegisterUser.verification_code = code;
        this.dataRegisterUser.name = this.name + " " + this.lastName;
        this.dataRegisterUser.email = this.email;
        this.dataRegisterUser.password = this.password;
        this.registerUserAction(this.dataRegisterUser);
      }
    }
  }
};
</script>
